.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spn {
  from {
    transform: rotate(deg);
  }

  to {
    transform: rotate(360deg);
  }
}

img {
  display: none;
}

.row {
  display: flex;
  padding: 15px;
  justify-content: space-between;
}